@font-face {
  font-family: "eurostile";
  src: url("../../../assets/font/eurostile.woff");
}
.nav {
  position: sticky;
  top: 0;
  z-index: 999;
  backdrop-filter: blur(3.125rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3.125rem 0;
  border-bottom: 1px solid var(--border);
}
.nav #icon {
  width: 2.1875rem;
  cursor: pointer;
  margin: 0.625rem 0;
}
.nav .dev-name {
  color: var(--text-blue);
  font-size: 1.375rem;
  cursor: pointer;
  font-weight: 700;
}
.nav__items {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 1.5625rem;
  padding: 0;
}
@media (max-width: 39.9375em) {
  .nav__items {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: -100%;
    top: 3.125rem;
    padding: 0.9375rem 0;
    text-align: center;
    justify-content: center;
    transition: all 0.3s linear;
    border-radius: 0.3125rem;
    box-shadow: 0 0.0625rem 0.3125rem 0.0625rem var(--box-shadow);
  }
}
.nav__items.active {
  left: 1.25rem;
  right: 1.25rem;
  background-color: var(--app-background);
}
.nav__link {
  position: relative;
  font-weight: 500;
  font-size: 1.125rem;
  padding-bottom: 0.3125rem;
}
.nav__link.active {
  color: var(--active-link);
}
.nav__marker {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0.1875rem;
  width: 0;
  background-color: var(--active-link);
  transition: all 500ms;
  border-radius: 0.625rem;
}
@media (max-width: 39.9375em) {
  .nav__marker {
    display: none;
  }
}
.nav .hamburger {
  display: none;
  cursor: pointer;
}
.nav .hamburger .bar {
  display: block;
  width: 1.5625rem;
  height: 0.1875rem;
  margin: 0.3125rem auto;
  transition: transform 0.3s ease-in-out;
  background-color: var(--text-secondary);
}
.nav .hamburger.active .bar:nth-child(2) {
  opacity: 0;
}
.nav .hamburger.active .bar:nth-child(1) {
  transform: translateY(0.5rem) rotate(45deg);
}
.nav .hamburger.active .bar:nth-child(3) {
  transform: translateY(-0.5rem) rotate(-45deg);
}
@media (max-width: 39.9375em) {
  .nav .hamburger {
    display: block;
  }
}
.nav__last-child {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
}

.intro {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  height: calc(100vh - 3.75rem);
}
.intro__hello {
  opacity: 0;
  animation: upToDown 0.2s linear forwards;
}
.intro__hello .wave-hand {
  background-image: url("../../../assets/images/wave-hand.png");
  width: 2.5rem;
  height: 2.5rem;
  margin-left: 0.625rem;
}
@keyframes wave {
  from {
    transform: none;
  }
  15% {
    transform: translate3d(-20%, 0, 0) rotate3d(0, 0, 1, -10deg);
  }
  30% {
    transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 7deg);
  }
  45% {
    transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -10deg);
  }
  60% {
    transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 5deg);
  }
  75% {
    transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -2deg);
  }
  to {
    transform: none;
  }
}
.intro__hello .wave-hand:hover {
  animation: wave 1s;
}
.intro__hello, .intro__tagline {
  font-size: 2.5rem;
  font-weight: 300;
}
@media (max-width: 69.9375em) {
  .intro__hello, .intro__tagline {
    font-size: 2rem;
  }
}
@media (max-width: 39.9375em) {
  .intro__hello, .intro__tagline {
    font-size: 1.75rem;
  }
}
.intro__tagline {
  max-width: 50rem;
  opacity: 0;
  animation: upToDown 0.4s linear 0.2s forwards;
}
.intro__tagline .name {
  color: var(--text-primary);
  font-weight: 500;
}
.intro__contact {
  font-size: 1.375rem;
  font-weight: 400;
  opacity: 0;
  animation: upToDown 0.6s linear 0.4s forwards;
}
.intro__contact .social-media-icon {
  margin-left: 1.25rem;
}
@media (max-width: 39.9375em) {
  .intro__contact {
    font-size: 1rem;
  }
}
.intro .highlight-link {
  transition: all 0.3s ease-in-out;
  font-weight: 400;
  text-decoration: none;
  display: inline-block;
  text-decoration: none;
  box-shadow: inset 0 -0.1875rem 0 var(--text-blue);
  padding: 0 0.3125rem;
  cursor: pointer;
  margin-left: 0.3125rem;
}
.intro .highlight-link:hover {
  color: var(--text-white);
  box-shadow: inset 0 -2.375rem 0 0 var(--text-blue);
}
@keyframes upToDown {
  0% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.skills {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 69.9375em) {
  .skills {
    grid-template-columns: repeat(2, 1fr);
  }
}
.skills__category-label {
  font-weight: 700;
  font-size: 1.25rem;
  color: var(--text-primary);
  text-transform: uppercase;
}
.skills ul {
  padding: 0;
}
.skills__category-item {
  margin: 0.1875rem 0;
  font-size: 1rem;
  color: var(--text-secondary);
}

.section {
  display: flex;
  justify-content: center;
}
@media (max-width: 39.9375em) {
  .section {
    flex-direction: column;
  }
}
.section__title {
  font-family: "eurostile";
  flex-shrink: 0;
  width: 12.5rem;
  margin-right: 6.25rem;
  color: var(--text-blue);
  text-transform: uppercase;
  text-align: right;
  font-weight: 700;
  letter-spacing: 0.125rem;
  font-size: 1.25rem;
  white-space: nowrap;
}
@media (max-width: 39.9375em) {
  .section__title {
    width: 0;
    margin-right: 0;
    margin-bottom: 1.25rem;
    text-align: left;
  }
}
.section__content {
  font-weight: 300;
  font-size: 1rem;
  width: 100%;
  max-width: 40.625rem;
  text-align: justify;
}
@media (max-width: 39.9375em) {
  .section__content {
    padding-left: 3.125rem;
  }
}

.about__myself {
  font-size: 1.125rem;
}
.about__myself .link {
  font-weight: 600;
  text-decoration: none;
  font-weight: 700;
  position: relative;
  display: inline-block;
}
.about__skills {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 1.875rem;
  margin: 1.875rem 0;
}
.about__skills .skill {
  text-transform: uppercase;
}
.about__skills .skill .count {
  font-size: 3.25rem;
  font-weight: 700;
  color: var(--text-blue);
}
.about__skills .skill .verify-skill {
  cursor: pointer;
  font-size: 1.25rem;
  font-weight: 500;
}
.about__skills .skill .verify-skill:after {
  content: "";
  display: inline-block;
  transition: all 0.2s ease-in-out;
  position: relative;
  bottom: 0.125rem;
  margin-left: 0.3125rem;
  background: url("../../../assets/images/arrow.png") no-repeat center;
  transform: rotate(-45deg);
  background-size: 100%;
  width: 1.25rem;
  height: 1.25rem;
  vertical-align: middle;
}
.about__skills .skill .verify-skill:hover:after {
  transform: translateX(0.4375rem);
}

.resume {
  text-transform: capitalize;
}
.resume .arrow-link {
  margin: 3.125rem 0;
}

.projects .section__content {
  display: grid;
  row-gap: 3.125rem;
}
.projects .section__content .seeMore {
  text-transform: capitalize;
}
.projects .section__content .project__img img {
  width: 100%;
}
.projects .section__content .project__name h4 {
  margin-top: 0.625rem;
  margin-bottom: 0.625rem;
  font-size: 1.375rem;
  padding: 0;
  text-transform: capitalize;
}
.projects .section__content .project__technologies ul {
  padding: 0;
}
.projects .section__content .project__technologies li {
  display: inline-block;
  padding: 0.4375rem 0.4375rem 0.3125rem;
  margin-right: 0.3125rem;
  margin-bottom: 0.625rem;
  color: var(--text-secondary);
  border: 0.125rem solid var(--border);
  border-radius: 0.1875rem;
  text-transform: uppercase;
  font-size: 0.625rem;
  font-weight: 500;
  letter-spacing: 0.03125rem;
  user-select: none;
}
.projects .section__content .project__button {
  margin: 1.875rem 0;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  row-gap: 0.625rem;
  column-gap: 0.625rem;
}
.projects .section__content .project__button a {
  padding: 0.625rem 0.9375rem;
  background: 0 0;
  border: 0.125rem solid var(--text-blue);
  border-radius: 0.1875rem;
  margin: 0 0.3125rem;
  color: var(--text-blue) !important;
  font-weight: 500;
  text-transform: uppercase;
  transition: all 0.3s ease;
  font-size: 1rem;
}
.projects .section__content .project__button a:hover {
  background: #00b7c7;
  color: #f2f2f2 !important;
}

.arrow-link {
  display: inline-block;
  font-size: 1.25rem;
  color: var(--text-secondary);
  transition: all 0.2s ease-in-out;
  font-weight: 500;
  background-color: transparent;
  white-space: nowrap;
}
.arrow-link:after {
  content: "";
  display: inline-block;
  transition: all 0.2s ease-in-out;
  position: relative;
  bottom: 0.125rem;
  margin-left: 0.9375rem;
  background: url("../../../assets/images/arrow.png") no-repeat center;
  background-size: 100%;
  width: 1.25rem;
  height: 1.25rem;
  vertical-align: middle;
}
.arrow-link:hover:after {
  transform: translateX(0.4375rem);
}

.contact .section__sub-title {
  font-family: "eurostile";
  font-weight: lighter;
  font-size: 1.75rem;
  margin-bottom: 1.5625rem;
  background: -webkit-linear-gradient(var(--text-secondary), var(--text-secondary), var(--app-background));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
@media (max-width: 69.9375em) {
  .contact .section__sub-title {
    font-size: 1.5625rem;
  }
}

.form {
  display: grid;
  row-gap: 1.25rem;
  margin-bottom: 1.25rem;
}
.form__control label {
  display: block;
  font-size: 1.25rem;
  padding-bottom: 0.3125rem;
  font-family: "eurostile";
}
.form__control input,
.form__control textarea {
  width: 100%;
  padding: 0.625rem;
  border-radius: 0.3125rem;
  outline: none;
  color: var(--text-secondary);
  background-color: var(--app-background);
  border: 0.125rem solid var(--border);
  font-size: 1rem;
}
.form__control textarea {
  resize: vertical;
}
.form__control input[type=submit] {
  width: 25%;
  padding: 0.625rem 0.9375rem;
  border: 0.125rem solid var(--border);
  border-radius: 0.1875rem;
  font-weight: 500;
  text-transform: uppercase;
  transition: all 0.3s ease;
  font-size: 1rem;
}
.form__control input[type=submit]:hover {
  background: var(--text-blue);
  color: var(--text-white);
}
@media (max-width: 39.9375em) {
  .form__control input[type=submit] {
    width: 100%;
  }
}

.animate {
  visibility: hidden;
  opacity: 0;
  transform: translateY(-30px);
  transition: opacity 0.6s cubic-bezier(0.694, 0, 0.335, 1) 0s, transform 0.6s cubic-bezier(0.694, 0, 0.335, 1) 0s;
}

:root {
  --selected-bg: hsl(52, 100%, 50%);
  --selected-text: hsl(240, 5%, 22%);
  --app-background: hsl(0, 0%, 100%);
  --text-blue: hsl(211, 100%, 50%);
  --text-primary: hsl(240, 9%, 29%);
  --text-secondary: hsl(240, 9%, 29%);
  --active-link: hsl(167, 100%, 26%);
  --box-shadow: hsla(0, 0%, 0%, 0.2);
  --border: hsl(220, 13%, 91%);
  --text-white: hsl(0, 0%, 95%);
}

body.night {
  --app-background: hsl(222, 27%, 12%);
  --text-secondary: hsl(240, 11%, 72%);
  --text-primary: hsl(0, 0%, 91%);
  --active-link: hsl(167, 100%, 33%);
  --box-shadow: hsla(0, 0%, 100%, 0.2);
  --border: hsl(0, 0%, 38%);
}

*,
:after,
:before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  scroll-padding-top: 75px;
}

::selection {
  background: var(--selected-bg);
}

body,
html {
  margin: 0;
  padding: 0;
  background-color: var(--app-background);
  color: var(--text-secondary);
  font-family: "Inter", sans-serif;
  font-display: optional;
  line-height: 1.5;
  width: 100%;
}

body.night ::selection,
html.night ::selection {
  color: var(--selected-text);
  background: var(--selected-bg);
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  outline: none;
  border: none;
  color: var(--text-secondary);
}

li {
  list-style: none;
}

main {
  display: grid;
  gap: 50px;
}

button {
  border: none;
}

.emoji {
  display: inline-block;
  vertical-align: text-top;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.container {
  width: 100%;
  padding: 0 clamp(1.375rem, 1.2rem + 0.89vw, 2rem);
  max-width: 70rem;
  margin: 0 auto;
}
@media (min-width: 70em) {
  .container {
    padding: 0;
  }
}