@use "../util/" as *;

.projects {
  .section__content {
    display: grid;
    row-gap: rem(50);

    .seeMore {
      text-transform: capitalize;
    }

    .project {
      &__img img {
        width: 100%;
      }
      &__name {
        h4 {
          margin-top: rem(10);
          margin-bottom: rem(10);
          font-size: rem(22);
          padding: 0;
          text-transform: capitalize;
        }
      }
      &__technologies {
        ul {
          padding: 0;
        }
        li {
          display: inline-block;
          padding: rem(7) rem(7) rem(5);
          margin-right: rem(5);
          margin-bottom: rem(10);
          color: var(--text-secondary);
          border: rem(2) solid var(--border);
          border-radius: rem(3);
          text-transform: uppercase;
          font-size: rem(10);
          font-weight: 500;
          letter-spacing: rem(0.5);
          user-select: none;
        }
      }
      &__button {
        margin: rem(30) 0;
        display: flex;
        justify-content: start;
        flex-wrap: wrap;
        row-gap: rem(10);
        column-gap: rem(10);
        a {
          padding: rem(10) rem(15);
          background: 0 0;
          border: rem(2) solid var(--text-blue);
          border-radius: rem(3);
          margin: 0 rem(5);
          color: var(--text-blue) !important;
          font-weight: 500;
          text-transform: uppercase;
          transition: all 0.3s ease;
          font-size: rem(16);
        }
        a:hover {
          background: #00b7c7;
          color: #f2f2f2 !important;
        }
      }
    }
  }
}
